
/* 
   Attribute 
*/
export class Attribute {
 
    // Attribute name 
    public name: string;
 
    // Attribute value type: string, int, bool 
    public type: string;
 
    // The string value in case of attribute type: string 
    public valString: string;
 
    // The int value in case of attribute type: int 
    public valInt: number;
 
    // The boolean value in case of attribute type: bool 
    public valBool: boolean;
 
    constructor(name?: string, type?: string, valString?: string, valInt?: number, valBool?: boolean) { 
        this.name = name;
        this.type = type;
        this.valString = valString;
        this.valInt = valInt;
        this.valBool = valBool;
    }

}


