import { SocketEntityChangeFilterPayload } from '../sockets/SocketEntityChangeFilterPayload';

/* 
   @WebSocketMessage
   This message is sent to the server to set filter criteria for the entity type 
*/
export class SocketEntityChangeFilter {
 
    // Message OpCode (100) 
    public op: number;
 
    // Message payload 
    public payload: SocketEntityChangeFilterPayload;
 
    constructor(op?: number, payload?: SocketEntityChangeFilterPayload) { 
        this.op = op;
        this.payload = payload;
    }

}


