import { LongTuple } from '../common/LongTuple';

/* 
   Account specific settings 
*/
export class AccountSettings {
 
    // Operational (dynamic) data retention time in days (1 - 365) 
    public retentionDays: number;
 
    // Drawing color of detected objects 
    public objectColors: LongTuple[];
 
    // Drawing color of rules lines and polygons 
    public ruleColor: number;
 
    // Drawing color of ignore mask polygon 
    public maskColor: number;
 
    // Flag to indicate that anomaly rule is enabled by default 
    public enableAnomalyByDefault: boolean;
 
    // Display date format (default: YYYY-MM-DD) 
    public dateFormat: string;
 
    // Display date time format (default: YYYY-MM-DD HH:mm:ss) 
    public dateTimeFormat: string;
 
    // Video stream source URI for the video player (RFC 6570 format) 
    public playerSourceUri: string;
 
    constructor(retentionDays?: number, objectColors?: LongTuple[], ruleColor?: number, maskColor?: number, enableAnomalyByDefault?: boolean, dateFormat?: string, dateTimeFormat?: string, playerSourceUri?: string) { 
        this.retentionDays = retentionDays;
        this.objectColors = objectColors;
        this.ruleColor = ruleColor;
        this.maskColor = maskColor;
        this.enableAnomalyByDefault = enableAnomalyByDefault;
        this.dateFormat = dateFormat;
        this.dateTimeFormat = dateTimeFormat;
        this.playerSourceUri = playerSourceUri;
    }

}


